.wishes-section {
	position: relative;
	.side-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		@include max-md {
			width: 100px;
		}
		@include max-xs {
			width: 80px;
		}
	}
	.side-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: scaleX(-1);
		@include max-md {
			width: 100px;
		}
		@include max-xs {
			width: 80px;
		}
	}
	.side-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.side-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: scaleY(-1);
	}
	.ornaments-wrapper {
		overflow: hidden;
		.orn-1 {
			position: absolute;
			bottom: 5rem;
			left: 0;
			width: 240px;
			transform: translate(-70%, 0);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			.orn-2 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 200px;
				transform: translate(15%, 55%);
				@include min-md {
					width: 220px;
				}
				@include min-lg {
					width: 240px;
				}
				.orn-3 {
					position: absolute;
					bottom: 0;
					right: 0;
					width: 120px;
					transform: translate(-20%, 40%);
					@include min-md {
						width: 140px;
					}
					@include min-lg {
						width: 160px;
					}
				}
			}
			.orn-4 {
				position: absolute;
				top: 0;
				right: 0;
				width: 120px;
				transform: translate(-5%, 60%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
				.orn-5 {
					position: absolute;
					top: 0;
					right: 0;
					width: 80px;
					transform: translate(-25%, -65%);
					@include min-md {
						width: 100px;
					}
					@include min-lg {
						width: 120px;
					}
				}
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 5rem;
			right: 0;
			width: 240px;
			transform: translate(70%, 0) scaleX(-1);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			.orn-7 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 200px;
				transform: translate(15%, 55%);
				@include min-md {
					width: 220px;
				}
				@include min-lg {
					width: 240px;
				}
				.orn-8 {
					position: absolute;
					bottom: 0;
					right: 0;
					width: 120px;
					transform: translate(-20%, 40%);
					@include min-md {
						width: 140px;
					}
					@include min-lg {
						width: 160px;
					}
				}
			}
			.orn-9 {
				position: absolute;
				top: 0;
				right: 0;
				width: 120px;
				transform: translate(-5%, 60%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
				.orn-10 {
					position: absolute;
					top: 0;
					right: 0;
					width: 80px;
					transform: translate(-25%, -65%);
					@include min-md {
						width: 100px;
					}
					@include min-lg {
						width: 120px;
					}
				}
			}
		}
		.orn-11 {
			position: absolute;
			top: 0;
			left: 0;
			width: 240px;
			transform: translate(-60%, 0) scaleX(-1);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			.orn-12 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 200px;
				transform: translate(10%, 30%);
				@include min-md {
					width: 220px;
				}
				@include min-lg {
					width: 240px;
				}
			}
			.orn-13 {
				position: absolute;
				top: 0;
				left: 0;
				width: 100px;
				transform: translate(45%, 0) rotate(-20deg) scaleX(-1);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
			}
		}
		.orn-14 {
			position: absolute;
			top: 0;
			right: 0;
			width: 240px;
			transform: translate(60%, 0);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			.orn-15 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 200px;
				transform: translate(10%, 30%);
				@include min-md {
					width: 220px;
				}
				@include min-lg {
					width: 240px;
				}
			}
			.orn-16 {
				position: absolute;
				top: 0;
				left: 0;
				width: 100px;
				transform: translate(45%, 0) rotate(-20deg) scaleX(-1);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		padding: 10rem 0;
		background-color: var(--color-primary);
		@include max-lg {
			padding: 8rem 0;
		}
		@include max-md {
			padding: 5rem 0;
		}
		.image-element {
			.wishes-background {
				width: 100%;
				height: auto;
				aspect-ratio: 7/8;
				object-fit: cover;
				border-radius: 4px;
				border: 2px solid #fff;
			}
		}
	}
	.wishes-form-wrapper {
		position: relative;
		padding: 0 5rem;
		@include max-lg {
			padding: 0 3rem;
		}
		.orn-99 {
			position: relative;
			width: 100px;
			margin: 0 auto 1rem;
		}
	}
	.wishes-content {
		position: relative;
		display: flex;
		align-items: stretch;
		overflow: hidden;
		@include max-md {
			flex-direction: column;
		}
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		position: relative;
		z-index: 3;
		&.card {
			border: none;
		}
		.card-body {
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
			.form-control {
				font-family: $font-primary;
				border-color: var(--color-dark);
				background-color: transparent;
				color: var(--color-dark);
				&::placeholder {
					color: var(--color-dark);
				}
				&:focus {
					box-shadow: none;
					background-color: transparent;
					color: var(--color-dark);
				}
			}
		}
	}

	.wishes-preview {
		position: relative;
		background-color: var(--color-primary);
		padding: 0 5rem;
		z-index: 5;
		@include max-lg {
			padding: 0;
		}
		.card {
			position: relative;
			border: none;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1.5rem;
				}
				.wishes {
					text-align: left;
					max-height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					color: var(--color-dark);
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.8);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.8);
							}
						}
					}
				}
			}
		}
	}
}
